// ScrollViewTouchEnd.ts
import { _decorator, Component, Node, ScrollView, EventTouch } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('ScrollViewTouchEnd')
export class ScrollViewTouchEnd extends Component {

    @property(ScrollView)
    scrollView: ScrollView = null;

    onLoad() {
        this.scrollView.node.on(Node.EventType.TOUCH_END, this.onTouchEnd, this);
    }

    onTouchEnd(event: EventTouch) {
        console.log('Touch ended');
        // 在此处添加你的处理代码
        event.propagationStopped = false;
    }
}
